@media (max-width: 768px) {
    .popup-content {
        margin:auto;
        background:rgba(184, 184, 184, 0.521);
        width: 95%;
        height: 90vh;
        padding:5px;
        margin-top: 5vh;
        margin-bottom: 5vh;
    } 
}

@media (min-width: 768px) {
    .popup-content {
        margin:auto;
        background:rgba(184, 184, 184, 0.521);
        width:80%;
        height: 80%;
        padding:5px;
    } 
}

[role=tooltip].popup-content {
    width:200px;
    box-shadow:0 0 3px rgba(0,0,0,.16);
    border-radius:5px
}

.popup-overlay {
    background:rgba(0,0,0,.5)
}

[data-popup=tooltip].popup-overlay {
    background:transparent
}

.popup-arrow {
    -webkit-filter:drop-shadow(0 -3px 3px rgba(0,0,0,.16));
    filter:drop-shadow(0 -3px 3px rgba(0,0,0,.16));
    color:#fff;
    stroke-width:2px;
    stroke:#d7d7d7;
    stroke-dasharray:30px;
    stroke-dashoffset:-54px
}

@media (max-width: 768px) {
    .project-content {
        margin:auto;
        background:rgba(25, 25, 25, 0.925);
        width:95%;
        max-height: 90vh;
        padding: 10vh 5% 2.5% 5%;
        animation: fadeIn linear 0.5s;
    }
}

@media (min-width: 768px) {
    .project-content {
        margin:auto;
        background:rgba(25, 25, 25, 0.925);
        width:80%;
        max-height: 80%;
        padding: 2.5% 5% 2.5% 5%;
        animation: fadeIn linear 0.5s;
    }
}


.project-exit-content {
    margin:auto;
    background:rgba(90, 90, 90, 0.925);
    width:80%;
    max-height: 80%;
    padding: 5%;
    animation: fadeOut linear 0.5s;
}